import * as actions_types from "../constants/action-types"

export const elements = (state = {}, action)=>{
  switch(action.type){
    case actions_types.GET_ALL_COMPONENTS_START:
      return {loading : true, data : null};
    case actions_types.GET_ALL_COMPONENTS_SUCCESS:
      return {loading : false, data : action.data};
    case actions_types.GET_ALL_COMPONENTS_FAILURE:
      return {loading : false, data : null};
    default :
      return state
  }
}