/**
 * s, p, d, f, g, h, i, k, l,
 * @param {Array} shells e.g Pt : [2, 8, 18, 32, 17, 1]
 */
export function getOrbitalsFromElectronShells(shells = []){
  const result = {}
  shells.forEach((e_count, n)=>{
    if (e_count === 2 * (n + 1) * (n + 1) ) return;
    if(e_count <= 2){ // s orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : e_count,
        t : e_count
      }
    }if(e_count <= 8 && e_count > 2){ // p orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : 2
      }
      result.p = {
        n : n + 1,
        l : "p",
        e : e_count - 2,
        t : e_count
      }
    }if(e_count <= 18 && e_count > 8){ // d orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : 2
      }
      result.p = {
        n : n + 1,
        l : "p",
        e : 6,
      }
      result.d = {
        n : n + 1,
        l : "d",
        e : e_count - 8,
        t : e_count
      }
    }if(e_count <= 32 && e_count > 18){ // f orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : 2
      }
      result.p = {
        n : n + 1,
        l : "p",
        e : 6,
      }
      result.d = {
        n : n + 1,
        l : "d",
        e : 10
      }
      result.f = {
        n : n + 1,
        l : "f",
        e : e_count - 18
      }
    }if(e_count <= 50 && e_count > 32){ // g orbital empty
      result.s = {
        n : n + 1,
        l : "s",
        e : 2
      }
      result.p = {
        n : n + 1,
        l : "p",
        e : 6,
      }
      result.d = {
        n : n + 1,
        l : "d",
        e : 10
      }
      result.f = {
        n : n + 1,
        l : "f",
        e : 14
      }
      result.g = {
        n : n,
        l : "g",
        e : e_count - 32,
      }
    }
  })
  return result;
}

export const color2shade = (col, amt) => {
  col = col.replace(/^#/, '')
  if (col.length === 3) col = col[0] + col[0] + col[1] + col[1] + col[2] + col[2]

  let [r, g, b] = col.match(/.{2}/g);
  ([r, g, b] = [parseInt(r, 16) + amt, parseInt(g, 16) + amt, parseInt(b, 16) + amt])

  r = Math.max(Math.min(255, r), 0).toString(16)
  g = Math.max(Math.min(255, g), 0).toString(16)
  b = Math.max(Math.min(255, b), 0).toString(16)

  const rr = (r.length < 2 ? '0' : '') + r
  const gg = (g.length < 2 ? '0' : '') + g
  const bb = (b.length < 2 ? '0' : '') + b

  return `#${rr}${gg}${bb}`
}

export function vw2px(value) {
  var w = window,
    d = document,
    e = d.documentElement,
    g = d.getElementsByTagName('body')[0],
    x = w.innerWidth || e.clientWidth || g.clientWidth,
    y = w.innerHeight|| e.clientHeight|| g.clientHeight;

  var result = (x*value)/100;
  // document.getElementById("result_vw_px").innerHTML = result;  // affichage du résultat (facultatif)
  return result;
}

export function vh2px(value) {
  var w = window,
    d = document,
    e = d.documentElement,
    g = d.getElementsByTagName('body')[0],
    x = w.innerWidth || e.clientWidth || g.clientWidth,
    y = w.innerHeight|| e.clientHeight|| g.clientHeight;

  var result = (y*value)/100;
  // document.getElementById("result_vh_px").innerHTML = result;  // affichage du résultat (facultatif)
  return result;
}