// import * as styles from '../assets/css/element.css'
import '../assets/css/element.css'
import {ELEMENT_2_BACKGROUND} from "../constants/periodic-tables"
import { getOrbitalsFromElectronShells } from '../utils';
import { Fade } from "react-awesome-reveal";
import { useState } from 'react';

export function Element({name, symbol, number, details = {}, height = "5vw", width = "5vw", left = "0", top = "0", ...others}){
  // function centre(element, y) {
    // element.setAttribute("transform",
                          // "translate(" + (500 - element.getBBox().width) / 2 + ", " + y + ")");
  // }
  const [onhover, set_onhover] = useState(false);
  const element_name = name || details.name;
  const element_symbol = symbol || details.symbol || "Hs";
  const atomic_number = details.atomic_number;
  const atomic_mass = parseFloat(details.atomic_mass || 240.3).toFixed(2);
  console.log(details.density);
  const electro_negativity = details.density ? parseFloat(details.density).toFixed(2) : "";
  const background = others.background || ELEMENT_2_BACKGROUND[details.category.split(",")[0]];
  const orbitals = getOrbitalsFromElectronShells(details.shells);
  const econf = details.econf || ""
  // console.log("Styles : ", styles);

  return (
      <div className = "react-periodic-table-element-shine-background" style = {{
        position : 'absolute',
        cursor : "pointer",
        left,
        top,
        height : width,
        width,
      }}>
        {/* <div>
          <button className = "react-periodic-table-element-shine-background" style = {{position : "relative"}} >"Click Me"</button>
        </div> */}
        <svg viewBox="0 0 506.26 506.26">
          <defs>
            <linearGradient id="linear-gradient" x1="0.5" y1="505.76" x2="505.76" y2="0.5" gradientUnits="userSpaceOnUse">
              <stop offset="0" stopColor="#ff7300" stopOpacity="0.3"/>
              <stop offset="0.17" stopColor="#f98400" stopOpacity="0.32"/>
              <stop offset="0.46" stopColor="#e9b201" stopOpacity="0.38"/>
              <stop offset="0.53" stopColor="#e5bd01" stopOpacity="0.4"/>
              <stop offset="1" stopColor="#fff" stopOpacity="0.5"/>
            </linearGradient>
            <radialGradient id="radial-gradient" cx="253.13" cy="253.13" r="253.13" gradientUnits="userSpaceOnUse">
              <stop offset="0" stopColor="#fff"/>
              <stop offset="1"/>
            </radialGradient>
          </defs>
          <title>{element_name}</title>
          <g id="gradient-background">
            <rect id="gradient" x="0.5" y="0.5" width="505.26" height="505.26" strokeMiterlimit="10" fill={background} stroke="url(#radial-gradient)"/>
          </g>
          <g id="primary-layer">
            <g id="Element" transform = "translate(0, 225)">
              <text transform="translate(250, 0)" fontSize="175" fontFamily="Cambria, Cambria" textAnchor = "middle">{element_symbol}</text>
              <text transform="translate(250, 90)" fontSize="79" fill="#1a1a1a" fontFamily="Cambria, Cambria" textAnchor = "middle">{element_name}</text>
            </g>
            <g id="AtomicNumber">
              <text transform="translate(10 90)" fontSize="96" fontWeight = "600" fontFamily="MyriadPro-Regular, Myriad Pro">{atomic_number}</text>
            </g>
            <g id="AtomicMass" transform = "translate(0, 400)">
              <text transform="translate(250 0)" fontSize="72" fontFamily="MyriadPro-Regular, Myriad Pro" textAnchor = "middle">{atomic_mass}</text>
            </g>
            <g id="ElectroNegativity">
              <text transform="translate(475 85)" fontSize="84" fontFamily="MyriadPro-Regular, Myriad Pro" textAnchor = "end">{electro_negativity}</text>
            </g>
            <g id="ElectroNegativity">
              <text transform="translate(250 490)" fontSize="72" fontFamily="MyriadPro-Regular, Myriad Pro" textAnchor = "middle">{econf}</text>
            </g>
          </g>
        </svg>
      </div>
  )
}

