import React from "react"
import { color2shade } from '../utils';
import * as constants from '../constants/periodic-tables';
import '../assets/css/element.css'

export function SemiFilledOrbit({title, shells = [], electronicConfiguration = null, color = "#ccebc5"}){
  if(!shells) return <>No Shell</>
  var orbits_colors = ["#ffff66","#66ff66", "#66ffff", "#ff6666"]
  var unfilled_shells = shells.map((ecount, n)=>({n : n + 1, ecount})).filter(({ecount, n})=>ecount !== 2 * n * n);
  var orbit_distance = 150 / unfilled_shells.length;
  var radius_iter = 100
  var orbits = unfilled_shells.map(({n, ecount})=>{
    var orbit =   <circle
          fill="none"
          stroke={orbits_colors[shells.length - n]}
          strokeWidth="3"
          cx="250"
          cy="250"
          r={radius_iter}
          style={{ opacity: 0.5 }}
        ></circle>
    var res = []
    var angle_iter = 0;
    var angle_size = Math.PI / (n * n);
    var electron_color = color2shade(orbits_colors[shells.length - n] , -100)
    for(var i = 0; i < ecount; i++){
      res.push(
        <circle fill={electron_color} stroke={electron_color} strokeWidth="3" cx={250 + radius_iter * Math.cos(-angle_iter)} cy={250 + radius_iter * Math.sin(-angle_iter)} r={5}>
        </circle>
      )
      angle_iter += angle_size;
    }
    
    radius_iter += orbit_distance;
    return (
      <React.Fragment>
          {orbit}
          {res}
      </React.Fragment>
    )
  });
  return (
    <svg
      viewBox="0 0 500 500"
      aria-label={title}
    >
      
      <circle
        fill={color}
        stroke={color}
        strokeWidth="4"
        cx="250"
        cy="250"
        r="40"
        style={{ opacity: 1 }}
      />
      {orbits}
      <text transform="translate(250, 255)"  fontSize="18" fontWeight = "700" textAnchor = "middle" >{title}</text>
    </svg>
  )
}

export function GroupIcon({group = 1, left, top, height, width}){
  var shells = constants.GROUP_2_SEMIFILED_ORBITS[group]
  return (
    <div   style = {{
      position : 'absolute',
      cursor : "pointer",
      left,
      top,
      height : width,
      width,
    }}>
      <div className = "react-periodic-table-atom-shine-background"><SemiFilledOrbit title = {`${group}`} shells = {shells}></SemiFilledOrbit></div>
      <div style = {{position : 'absolute', fontSize : "5%", bottom : "0px", width : "100%", backgroundColor : '#000000', color : "#FFFFFF", textAlign : "center"}} >{group}</div>
    </div>
  )
}