import * as elements from "../middleware/api/elements"
import * as actions_types from '../constants/action-types'

export const getAllElements = ()=>{
  return (dispatch, getState)=>{
    dispatch({
      type : actions_types.GET_ALL_COMPONENTS_START,
    });
    elements.getAllElementsList().then((data)=>{
      return dispatch({
        type : actions_types.GET_ALL_COMPONENTS_SUCCESS,
        data
      })
    }).catch((err)=>{
      return dispatch({
        type : actions_types.GET_ALL_COMPONENTS_FAILURE,
        err,
      })
    });
  }
}