import React, {useRef} from "react";

import {useSelector, useDispatch} from 'react-redux'

import Loader from 'react-loader-spinner'

import { Col, Container, Row } from "react-grid-system";

import {PeriodicTableLoader} from './Loader'
import { Element, ElementGuide } from "./Element";

import {vw2px} from '../utils'
import * as constants from '../constants/periodic-tables'
import { GroupIcon } from "./Atom";
const compact = {
  padding: 0,
  margin: 0
}

function ColorLabel({left, top, width, height, name, color}){
  return (
    <g transform = {`translate(${left} ${top})`}>
      <rect transform = "translate(-50% -50%)" x="-12.5" y="-12.5" width="25" height="25" fill = {color}></rect>
      <text transform="translate(30 0)" fontSize="13" fontWeight = "600" fontFamily="MyriadPro-Regular, Myriad Pro">{name}</text>
    </g>
  )
}

export function SVGMetaTable({colorLabels = []}){
  var Y = 100
  var xcord = 15, ywidth = Y / 6, ycord = 15 - ywidth, xoffset = 0;
  return (
    <svg viewBox="0 0 350 200">
        {
          colorLabels.map(([name, color], index)=>{
            ycord += ywidth;
            if (index > colorLabels.length / 2 ){
              xoffset = 165
            }
            return <ColorLabel left = {xcord + xoffset} top = {ycord % Y} name = {name} color = {color}></ColorLabel>
          })
        }
    </svg>
  )
}
/**
 * Total grid size is of 10 * 18 default
 * Padding
 * Margin
 */
export function PeriodicTableMetaInfo({top = "0%", left = "0%", height = "16%", width =  "16%", }){
  // how to calculate the position in entire grid
  
  const color_table_1 = [], color_table_2 = [],
   categories = Object.keys(constants.ELEMENT_2_BACKGROUND);
  function table_code(name, color){
    return (
              <tr>
                <td style = {{backgroundColor : color}}></td>
                <td style = {{textAlign : "left", paddingLeft: "10%"}}><b>{name}</b></td>
              </tr>
          )
  }
  for(var i = 0; i < categories.length / 2; i++){
      color_table_1.push(
            table_code(categories[i], constants.ELEMENT_2_BACKGROUND[categories[i]])
      )
      color_table_2.push(
            table_code(categories[Math.floor(i + categories.length / 2 )], constants.ELEMENT_2_BACKGROUND[categories[Math.floor(i + categories.length / 2)]])
      )
  }  
  return (
    <div               
      style = {{
        position : "absolute",
        top,
        left,
        width,
        height,
      }}
    >
      <ElementGuide></ElementGuide>
      <div style = {{
        position : "absolute",
        top : "10%",
        left : "50%",
        width : "45%",
        height : "50%",
        border : '1px dashed #7d7d7d',
        padding : "2px"
        
      }}>
        <SVGMetaTable colorLabels = {Object.entries(constants.ELEMENT_2_BACKGROUND)}></SVGMetaTable>
        
      </div>
    </div>
  )
  
}


export function PeriodicTable(){
  const elements = useSelector(state=>state.elements);
  const GRIDSIZE = [10, 18];
  return (
    <div 
      style = {{
        position : "absolute",
        marginLeft : "auto",
        marginRight : "auto",
        marginTop : "25px",
        width : vw2px(80),
        height : vw2px(80),
        textAlign : 'center'
      }}
    >
      <div style = {{
        marginTop : "-8%",
        fontWeight : "bold"
      }}>
        Periodic Table
      </div>
      {
        elements.loading
        ? <div style = {{height:vw2px(25), width:vw2px(25), margin:"auto"}}><PeriodicTableLoader
            visible = {elements.data}
            color="#000000"
            textAlign = "center"
        /></div>
        : elements.data.map(element=>{
          if(!element.xpos || !element.ypos) return null
          // console.log("element : ", element);
          var period = element.ypos, group = element.xpos, gridSize = GRIDSIZE;
          // const xlen_percentage = (1) / gridSize[0] * 100;
          const xlen_percentage = 1 / gridSize[1] * 100;
          const ylen_start = (period -  1) * xlen_percentage
          const xlen_start = (group -  1) * xlen_percentage;
          // console.log(period, group, xlen_start, ylen_start, xlen_percentage);
          return (
            <Element
              style = {{
                position : 'absolute'
              }}
              left = {`${xlen_start}%`}
              top = {`${ylen_start}%`}
              width = {`${xlen_percentage}%`}
              details = {element}
            >
            </Element>
          )      
        }).concat(
          // Adding group Icons
          
        )
      }
      {
        elements.loading 
        ? null
        : constants.GROUP_HEADS.map((element_no, group)=>{
          var element = elements.data.filter(element => element.atomic_number === element_no)[0];
          var period = element.ypos - 1;
          var group = element.xpos;
          var gridSize = GRIDSIZE;
          const width = 1 / gridSize[1] * 100;
          const top = (period -  1) * width
          const left = (group -  1) * width;
          return (
              <GroupIcon group = {group} top = {`${top}%`} left = {`${left}%`} height = {`${width}%`} width = {`${width}%`}></GroupIcon>
          );
        })
      }

      {
        elements.loading 
        ? null
        : [1].map(()=>{
          var period = 0 - 1 ;
          var group = 3 - 1;
          var gridSize = GRIDSIZE;
          const size = 1 / gridSize[1] * 100;
          const top = (period) * size
          const left = (group) * size;
          const height = 3 * size;
          const width = 10 * size;
          console.log("top, left , width, height : ", top, left, width);
          return (
              <PeriodicTableMetaInfo top = {`${top}%`} left = {`${left}%`} height = {`${height}%`} width = {`${width}%`}></PeriodicTableMetaInfo>
          );
        })
      }
    </div>
  )
}