import React from "react";
import PropTypes from "prop-types";

export const PeriodicTableLoader = props => (
  <svg
    // version="1.1"
    // width={props.width}
    // height={props.height}
    // xmlns="http://www.w3.org/2000/svg"
    // x="0px"
    // y="0px"
    viewBox="0 0 100 100"
    aria-label={props.label}
  >
    <linearGradient id="gradient-stroke" x1="1" y1="1" x2="0" y2="0">
       <stop offset="0%" stopColor={props.color}></stop>
       <stop offset="100%" stopColor={props.color} stopOpacity = {0}></stop>
    </linearGradient>
    {console.log(props)}
    <circle
      fill={props.color}
      stroke={props.color}
      strokeWidth="4"
      cx="50"
      cy="50"
      r={props.radius}
      style={{ opacity: 1 }}
    />
    <circle
      fill="none"
      stroke="url(#gradient-stroke)"
      strokeWidth="2"
      cx="50"
      cy="50"
      r={props.radius + 38}
      style={{ opacity: 0.5 }}
    >
      <animateTransform
        attributeName="transform"
        dur="2s"
        type="rotate"
        from="0 50 48"
        to="360 50 52"
        repeatCount="indefinite"
      />
    </circle>
    <circle fill={props.color} stroke={props.color} strokeWidth="3" cx="8" cy="54" r={props.radius}>
      <animateTransform
        attributeName="transform"
        dur="2s"
        type="rotate"
        from="0 50 48"
        to="360 50 52"
        repeatCount="indefinite"
      />
    </circle>
  </svg>
);

PeriodicTableLoader.propTypes = {
  height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  color: PropTypes.string,
  label: PropTypes.string,
  radius: PropTypes.number
};

PeriodicTableLoader.defaultProps = {
  height: 80,
  width: 80,
  color: "green",
  label: "audio-loading",
  radius: 6
};